import React, { useState, useEffect } from 'react';
import { useDispatch } from "react-redux";
import { userLogIn } from "../Reducer/userSlice.js";
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import "../../styles/Login.css"

function Login() {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const [user_id, setId] = useState('');
    const [user_pw, setPw] = useState('');

    const [msg, setMsg] = useState('');

    const server_login_url = '/member/login';
    const server_search_url = '/member/search';

    // const session_id = '<%=(String)session.getAttribute("uid")%>';

    const userIdHandler = (e) => {
        setId(e.target.value);
    }

    const userPwHandler = (e) => {
        setPw(e.target.value);
    }

    //Data 
    const userLoginData = {
        userId: user_id,
        userPw: user_pw
    }

    useEffect(() => {
        if (msg) {
            setMsg("");
        }
    }, [msg])

    //  
    const onClickLogin = (e) => {
        console.log(' ');

        // 
        e.preventDefault();
        if (!user_id) {
            return alert("Id ");
        }
        else if (!user_pw) {
            return alert(" ");
        }

        axios.post(server_login_url, JSON.stringify(userLoginData), {
            headers: {
                "Content-Type": 'application/json'
            },
        }).then(res => {
            // 
            if (res.data.result === 'success') {
                // sessionStorage.setItem('session_id', res.headers['session']);
                // console.log(sessionStorage.getItem('session_id'));
                console.log(res.headers);
                alert(' ');
                GetInfo()
                //Home 
                navigate('/');
            }
            // 
            else {
                alert(' ');
                setMsg("   ");
                console.log(res.data.result);
            }
        })
            .catch(error => {
                console.log(error);
            }
            )
    }

    // 
    const GetInfo = () => {
        axios.get(server_search_url, {
            headers: {
                "Content-Type": 'application/json',
            },

        }, { withCredentials: true, }).then(res => {
            // 
            if ((res.data.result) !== "authentication failed" || (res.data.result) !== "undefined error") {
                // sessionStorage.setItem('user_id', user_id);
                alert('   ');
                dispatch(userLogIn(res.data.result));
                console.log(res.data.result);
                //Home 
                navigate('/');
            }
            //  
            else {
                alert('   ');
                setMsg("   ");
                console.log(res.data.result);
            }
        })
            .catch(error => {
                console.log(error);
            }
            )
    }


    return (
        <div>
            <h2>Login Page</h2>
            {/*  */}
            <div>
                <label htmlFor='input_id'>ID : </label>
                <input type='text' name='user_id' placeholder=' ' value={user_id} onChange={userIdHandler} />
            </div>
            {/*  */}
            <div>
                <label htmlFor='input_pw'>PW : </label>
                <input type='password' name='user_pw' placeholder=' ' value={user_pw} onChange={userPwHandler} />
            </div>
            <span>
                <button type='button' onClick={onClickLogin}>Login</button>
            </span>
            <span>
                <span type='link' onClick={() => { navigate('/SignIn'); }}>SignIn</span>
            </span>
            <div className='msg'>
                {msg}
            </div>
        </div>
    );
};
export default Login;